.sendForm {
  margin-top: 50px;
  width: 100%;
  display: flex;
  gap: 20px;
}

.textarea {
  padding: 10px;
  background-color: rgb(239, 222, 241);
  width: 100%;
  height: 40px !important;
  border: none;
  border-radius: 20px;
  resize: none;
}

.button {
  width: 120px;
  height: 40px;
  border-radius: 20px;
  color: white;
  background-color: blueviolet;
  border: none;
}

.button:hover {
  border: 1px solid black;
}
.button:active {
  color: rgb(239, 222, 241);
  border: 3px solid rgb(239, 222, 241);
}
